import React from 'react'
import { Carousel, Image } from 'antd';

import './css/index.css'
//ws
import wszf from './img/ws/wszf.png'
import jsmb from './img/ws/jsmb.png'
import pm from './img/ws/pm.png'
//z1
import ts from './img/z1/ts.png'
import z1wk from './img/z1/wk.png'
import z1 from './img/z1/z1.png'
//z2
import z2 from './img/z2/z2.png'
import dzjj from './img/z2/dzjj.png'
import nyd from './img/z2/nyd.png'
import wds from './img/z2/wds.png'
import z2wk from './img/z2/wk.png'
//z3
import z3 from './img/z3/z3.png'
import tjbg from './img/z3/tjbg.png'
import bhzs from './img/z3/bhzs.png'

export default function Zmxl() {
    return (
        <div className='zmxl'>
            <div className='bcg'></div>
            <div className='head'>
                <h2>目录</h2>
                <p>
                    <a href="#xysy">闲言碎语</a>
                </p>
                <p>
                    <a href="#zmws">造梦无双</a>
                </p>
                <p>
                    <a href="#zm1">造梦西游1七魔王</a>
                </p>
                <p>
                    <a href="#zm2">造梦西游2十殿阎罗</a>
                </p>
                <p>
                    <a href="#zm3">造梦西游3大闹天庭</a>
                </p>
            </div>

            <h2 id='xysy'>闲言碎语</h2>
            <p>
                造梦系列十多年的铺垫，吸引了大批玩家。
                造1-3存档本地-云端存储，避免不了被修改，现在某宝也能买到BT修改账号。
                后面的作品改为服务器同步数据。付费玩家的权益得到更好的保障。
            </p>
            <p>
                一代代作品玩下来，能明显的感受到，界面排版，角色操作手感，画质，每一部都在不断提升。
                造梦一开始就非常重视版权，从2代就在各种地图场景里将公司符号（4399）融合进地图中。
            </p>

            <h2 id='zmws'>造梦无双</h2>
            <p>
                造梦无双的画质让我很惊讶，地图场景原画，比很多大型MMO都要好。
            </p>
            <p>
                无双的交互体验非常好，
                界面排版，冗余道具拆分合并，一些容易误操作的场景，宠物放生，道具丢弃分解等，都一一进行优化，这些并不能直接带来收益，但能为玩家
                带来更好的游戏体验，是玩家付费的基础。
                无双做的很精致，也有人气，B站与造梦无双相关内容的更新，每周都有几十个视频发布。
            </p>

            <p>
                我玩的进度战斗力113W+（P1），本区战斗力排29（P2）, P3万兽争锋刚开，趁没人速通了混个第3，刷新后实际排96（右下为真实排名）
            </p>
            <p>
                刚开始玩还是挺好玩的，一旦内容全打完，时间久了变成天天做任务的模式了。打完就不想玩了，又不舍得脱坑。
                PVP能玩的比较少，噩梦、幻境等多人合作闯关副本，已变成日常任务的形式，大部分玩家参与的目的从娱乐变成了获得资源。
            </p>
            <p>
                感觉大部分多人游戏都会这样。新内容更新不够快，玩家重复打副本乐趣减少，通过各类日常任务奖励道具保持日活。
            </p>

            <p>
                虽然服务器直连，但无双还是有一部分数据的计算在本地进行，比如单人闯关，好在官方处罚力度到位。并且资源分配也很合理，修改可获得的体验有限。
                更好的体验还是要慢慢做日常，积累资源，提升战力才能玩的到。
            </p>
            <p>
                有很多吐槽无双的视频，比如数值平衡，天道模式，有战力修正，为的是低战玩家不至于被高战拉开太大差距。本意是好，但是低战还是很难赢，
                而高战玩家玩起来，这么高的战力，却不能碾压低战，还可能被反杀。
                最终导致两边都不满意。
                关于天道模式，打的是玩家的战斗操作技巧，我认为，可以分两种方式优化。
            </p>
            <p>
                1 参考DNF目前的PK场设计，统一数值，玩家自己搭配技能。这里的差距只有战斗技巧。
            </p>
            <p>
                2 去掉战力修正，战力高就是碾压。保证付费群体的体验。
            </p>
            <p>
                充值内容做的已经非常精细了。但无双受限于2D，无双的多人交互玩法有限，而免费玩家群体的作用，大多是让游戏看起来有人气。
                目前无双没有玩家交易，自然也就没有DNF、梦幻那种一部分玩家“搬砖”，将资源卖给付费群体，官方从交易中抽成的模式。
            </p>


            <Image.PreviewGroup>
                <Carousel effect="fade" autoplay dotPosition='top' className='photo'>
                    <Image src={jsmb} alt="" />
                    <Image src={pm} alt="" />
                    <Image src={wszf} alt="" />
                    {/* <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/ws/jsmb.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/ws/pm.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/ws/wszf.png' alt="" /> */}
                </Carousel>
            </Image.PreviewGroup>

            <h2 id='zm1'>造梦西游1七魔王</h2>
            <p>
                梦开始的地方，小学的时候和弟弟一起玩的。内容相对少，但当时非常火，很多帖子讨论易爆点，打怪技巧。
            </p>
            <Image.PreviewGroup>
                <Carousel effect="fade" autoplay dotPosition='top' className='photo'>
                    <Image src={ts} alt="" />
                    <Image src={z1wk} alt="" />
                    <Image src={z1} alt="" />
                    {/* <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z1/ts.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z1/wk.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z1/z1.png' alt="" /> */}
                </Carousel>
            </Image.PreviewGroup>

            <h2 id='zm2'>造梦西游2十殿阎罗</h2>
            <p>
                造2出商城了，一些终极道具只能商城获取，但是因为单机问题，避免不了被破坏，后面也出了签到活动送这些道具了
            </p>

            <Image.PreviewGroup>
                <Carousel effect="fade" autoplay dotPosition='top' className='photo'>
                    <Image src={z2} alt="" />
                    <Image src={z2wk} alt="" />
                    <Image src={dzjj} alt="" />
                    <Image src={nyd} alt="" />
                    <Image src={wds} alt="" />
                    {/* <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z2/z2.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z2/wk.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z2/dzjj.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z2/nyd.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z2/wds.png' alt="" /> */}
                </Carousel>
            </Image.PreviewGroup>

            <h2 id='zm3'>造梦西游3大闹天庭</h2>
            <p>
                造3的至尊仓库，官方给的一种复制道具渠道，开两个页面，1个领取道具，另一个存档，反复刷，资源2倍叠加。
                和前作一样的问题，很多讨论修改的帖子，侧面也反应出，造梦在当时已经非常火了。
                造3最经典的作品，目前造3的贴吧也是很活跃，总有玩家回坑。
            </p>
            <Image.PreviewGroup>
                <Carousel effect="fade" autoplay dotPosition='top' className='photo'>
                    <Image src={z3} alt="" />
                    <Image src={tjbg} alt="" />
                    <Image src={bhzs} alt="" />
                    {/* <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z3/z3.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z3/tjbg.png' alt="" />
                    <Image src='https://raw.githubusercontent.com/HomeworkDone/test_ch/master/src/components/Casual/zmxl/img/z3/bhzs.png' alt="" /> */}
                </Carousel>
            </Image.PreviewGroup>

        </div>
    )
}
