import React from 'react';
import { Carousel, Image } from 'antd';

import './css/index.css'

import zy from './img/zy.png'

import bd from './img/bd.png'
import egt from './img/egt.png'
import gh from './img/gh.png'
import jzsx from './img/jzsx.png'
import qbgh from './img/qbgh.png'
import tree_reborn from './img/tree_reborn.png'
import qxjh from './img/qxjh.png'


export default function War3() {
    return (
      <div className='War3'>
        <div className='bcg'></div>
        <div className='head'>
          <h2>目录 魔兽</h2>
          <p>
          <a href="#xysy">闲言碎语</a>
          </p>
          <p>
            <a href="#achievement">战役</a>
          </p>
          <p>
            <a href="#xiugai">编辑器</a>
          </p>
        </div>
        <h2 id='xysy'>闲言碎语</h2>
        <p>
          我很喜欢玩原创地图，里面有各种各样的故事，有各种复刻，在里面还能玩魔兽版的红警2，暗黑2。
        </p>
        <p>
          自己也会用编辑器改一改，比如把常规地图的伊利丹模型换成战役里带翅膀的，设置可以水上行走，不过自动寻路时还是按陆地模式寻路。
          改装备，新造单位，做触发。
        </p>
        <p>
          暴雪重视魔兽争霸原创地图，18年看到过官方举办的RPG地图开发者大会。鼓励原创地图，对作者进行扶持，现在又推出了群星计划。
          原创地图的收益很高，地图有商城内购作为盈利模式。一些火爆地图，年流水上千万，对比国内的起凡，已经放弃了这个方向，太可惜了。
        </p>
        <p>
          <img src={qxjh} alt="" />
        </p>
        <h2 id='achievement'>战役</h2>
        <p>
          翻了翻以前的存档，找到一个兽族战役的记录。装备全收集，隐藏关全探索。暗夜精灵的有个影子之球，要找10个碎片。
        </p>
        <p>
          <img src={zy} alt="" />
        </p>
        <h2 id='xiugai'>编辑器</h2>
        <p>
          魔兽的编辑器我用的不多，改图做了个树木再生的触发，做了件带所有光环的装备。
          装备只能选1个技能，所以要选魔法书，给这个魔法书添加多个技能，然后触发里禁用该魔法书
        </p>
        <p>
          <img src={tree_reborn} alt="" />
        </p>
        <p>
          <img src={egt} alt="" />
        </p>

        <p>
          载入游戏测试
        </p>
        <p>
          <img src={qbgh} alt="" />
        </p>
        <p>
          可以给建筑回血回蓝
        </p>
        <p>
          <img src={jzsx} alt="" />
        </p>

        <Image.PreviewGroup>
        <Carousel effect="fade" autoplay dotPosition='top' className='gh'>
          <Image src={gh} alt="" />
          <Image src={bd} alt="" />
        </Carousel>
      </Image.PreviewGroup>
  
      </div>
    )
  }